import axios from 'axios';
import PageHelper from '@/utils/pageHelper';
import { handGetParent, handPostParent } from '@/utils/methodRequest';
import config from '@/config';

export const getList_PRODUCT = async (payload) => {
  const { data } = await axios.get('/product', {params: PageHelper.requestFormat(payload)});
  return PageHelper.responseFormat(data);
}
export const save_PRODUCT = async (payload) => {
  const { data } = await axios.post('/product', payload);
  if (data.message) config.notice.success(data.message);
  return data;
}
export const update_PRODUCT = async (payload) => {
  const { data } = await axios.put('/product/' + payload.id, payload);
  if (data.message) config.notice.success(data.message);
  return data;
}
export const delete_PRODUCT = async (payload) => {
  for (let index = 0; index < payload.length; index++) {
    const {data} = await axios.delete('/product/' + payload[index]);
    if (data.message) config.notice.success(data.message);
  }
  return null;
}
export const patch_PRODUCT = async (payload) => {
  for (let index = 0; index < payload.length; index++) {
    const {data} = await axios.patch('/product/' + payload[index].id, payload[index]);
    if (data.message) config.notice.success(data.message);
  }
  return null;
}

