export const change_PRODUCT_ATTRIBUTE = async (payload) => {
  let arrayId = [];
  for (let index = 0; index < payload.length; index++) {
    const method = payload[index].id > 0 ? 'put' : 'post';
    arrayId = arrayId.concat(payload[index]["attributes_ids"])
    const {data} = await axios[method]('/product-attribute/' + (method === 'put' ? payload[index].id : ''), payload[index]);
    if (data.message) config.notice.success(data.message);
  }
  arrayId = arrayId.filter((value, index, self) => self.indexOf(value) === index)
  await axios.put('/product/' + payload[0]["product_id"], { attributes_ids: arrayId });
  return null;
}
export const delete_PRODUCT_ATTRIBUTE = async (id) => {
  const { data } = await axios.delete('/product-attribute/' + id);
  if (data.message) config.notice.success(data.message);
  return null;
}
export const check_slug = async (obj) => {
  const { data } = await axios.post("/check-slug/", obj)
  return data.data;
}
