import modelEnhance from '@/utils/modelEnhance';
import PageHelper from '@/utils/pageHelper';

import {
  getList_PRODUCT,
  save_PRODUCT,
  update_PRODUCT,
  delete_PRODUCT,
  patch_PRODUCT,
  get_CATEGORY,
  change_CATEGORY,
  delete_CATEGORY,
  get_ATTRIBUTE,
  change_ATTRIBUTE,
  delete_ATTRIBUTE,
  get_BRAND,
  change_BRAND,
  delete_BRAND,
  change_PRODUCT_ATTRIBUTE,
  delete_PRODUCT_ATTRIBUTE,
} from "../service"

/**
* true when the page is first loaded
* Can use this value to prevent switching pages when
* Initialize data multiple times
*/
let url = true;
export default modelEnhance({
  namespace: 'product',

  state: {
    pageData: PageHelper.create(),
    categorys: [],
    brands: [],
    attributes: [],
  },

  subscriptions: {
    setup({ dispatch, history }) {
      history.listen(({ pathname, search }) => {
        if (pathname === '/product' && !!search && url) {
          url = false
          setTimeout(() => url = true, 500);
          const payload = PageHelper.URLToArray(search);
          dispatch({ type: 'init', payload });
        }
      });
    }
  },

  effects: {
    // Enter page load
    *init({ payload }, { put, select }) {
      const { pageData } = yield select(state => state.product);
      yield put({
        type: 'getPageInfo',
        payload: {
          pageData: pageData.setPage(payload)
        }
      });
      yield put({ type: 'getCategory' });
      yield put({ type: 'getBrand' });
      yield put({ type: 'getAttribute' });

    },
    // Get paging data
    *getPageInfo({ payload }, { call }) {
      const { pageData } = payload;
      const data = yield call (getList_PRODUCT, pageData)
      pageData.setPage(data);
    },
    // Save after querying paging
    *save({ payload }, { put, select, call }) {
      const { values, success } = payload;
      const { pageData } = yield select(state => state.product);

      if (values.id) yield call(update_PRODUCT, values);
      else yield call(save_PRODUCT, values);

      yield put({ type: 'getPageInfo', payload: { pageData } });
      success();
    },
    // Delete after querying paging
    *remove({ payload }, { put, select, call }) {
      const { records, success } = payload;
      const { pageData } = yield select(state => state.product);
      const data = records.map(item => item.id)
      yield call(delete_PRODUCT, data);
      yield put({ type: 'getPageInfo', payload: { pageData } });
      success();
    },
    *patch({ payload }, { select, call, put }) {
      const { records } = payload;
      const { pageData } = yield select(state => state.product);
      yield call(patch_PRODUCT, records);
      yield put({ type: 'getPageInfo', payload: { pageData } });
    },

    *changeProductAttribute({ payload }, { select, call, put }) {
      const { pageData } = yield select(state => state.product);
      yield call(change_PRODUCT_ATTRIBUTE, payload)
      yield put({ type: 'getPageInfo', payload: { pageData } });
    },
    *removeProductAttribute({ payload }, { select, call, put }) {
      const { pageData } = yield select(state => state.product);
      yield call(delete_PRODUCT_ATTRIBUTE, payload);
      yield put({ type: 'getPageInfo', payload: { pageData } });
    },

    *getCategory({ payload }, { call, put }) {
      const data = yield call(get_CATEGORY)
      yield put({ type: 'success', payload: data });
    },
    *changeCategory({ payload }, { call, put }) {
      const categorys = yield call(change_CATEGORY, payload);
      yield put({ type: 'success', payload: { categorys } });
    },
    *removeCategory({ payload }, { call, put }) {
      yield call(delete_CATEGORY, payload);
      yield put({ type: 'getCategory' });
    },

    *getAttribute({ payload }, { call, put }) {
      const data = yield call(get_ATTRIBUTE)
      yield put({ type: 'success', payload: data });
    },
    *changeAttribute({ payload }, { call, put }) {
      const attributes = yield call(change_ATTRIBUTE, payload)
      yield put({ type: 'success', payload: { attributes } });
    },
    *removeAttribute({ payload }, { call, put }) {
      yield call(delete_ATTRIBUTE, payload);
      yield put({ type: 'getAttribute' });
    },

    *getBrand({ payload }, { call, put }) {
      const data = yield call(get_BRAND)
      yield put({ type: 'success', payload: data });
    },
    *changeBrand({ payload }, { call, put }) {
      const brands = yield call(change_BRAND, payload)
      yield put({ type: 'success', payload: { brands } });
    },
    *removeBrand({ payload }, { call, put }) {
      yield call(delete_BRAND, payload);
      yield put({ type: 'getBrand' });
    }
  },

  reducers: {
    success(state, { payload }) {
      return {
        ...state,
        ...payload,
      };
    },
  }
});
